import React from "react";

export const TiposPlatillos = ({ tipos }) => {
  return (
    <>
      {tipos.map((tipo) => {
        return (
          <>
            <div className="d-inline">
              {tipo.alergeno ? (
                <div>
                  <h5 className="text-start">
                    {tipo.nombre}
                    {"  "}
                    <span class="badge rounded-pill text-bg-warning bad">
                      alergeno
                    </span>
                  </h5>
                </div>
              ) : (
                <>
                  <h5 className="text-start">{tipo.nombre}</h5>
                </>
              )}
            </div>
            <p className="text-start">{tipo.descripcion}</p>
            <h5 className="text-end">{tipo.precio}</h5>
          </>
        );
      })}
    </>
  );
};
