import React from "react";
import { Tipos } from "./Tipos";

export const MenuView = ({ vista, menu }) => {
  return (
    <div className="col">
      {vista == "todo"
        ? menu.map((menu_item) => {
            return (
              <Tipos
                nombre={menu_item.nombre}
                platillos={menu_item.platillos}
              />
            );
          })
        : menu.map((menu_item) => {
            if (menu_item.nombre == vista)
              return (
                <Tipos
                  nombre={menu_item.nombre}
                  platillos={menu_item.platillos}
                />
              );
          })}
    </div>
  );
};
