import React from "react";
import { Platillos } from "./Platillos";

export const Tipos = ({ nombre, platillos }) => {
  return (
    <>
      {" "}
      <h1 className="text-start">{nombre}</h1>
      <hr className="linea-menu-p" />
      {platillos.map((platillo) => {
        return <Platillos platillos={platillo} />;
      })}
    </>
  );
};
