import React from "react";
import { TiposPlatillos } from "./Tipos-platillos.jsx";

export const Platillos = ({ platillos }) => {
  return (
    <>
      {platillos.costo == "$0" ? (
        <>
          {" "}
          <div className="d-flex justify-content-between">
            <h3 className="text-start">{platillos.nombre}</h3>
          </div>
          <p>{platillos.descripcion}</p>
          <hr className="linea" />
          <TiposPlatillos tipos={platillos.tipos} />
        </>
      ) : (
        <>
          <div className="d-flex justify-content-between">
          {platillos.alergeno ? (
                <div>
                  <h5 className="text-start">
                    {platillos.nombre}
                    {"  "}
                    <span class="badge rounded-pill text-bg-warning bad">
                      alergeno
                    </span>
                  </h5>
                </div>
              ) : (
                <>
                  <h5 className="text-start">{platillos.nombre}</h5>
                </>
              )}
            <h5 className="text-end">{platillos.costo}</h5>
          </div>
          <p>{platillos.descripcion}</p>
        </>
      )}
      <hr className="linea-menu-p" />
      <br></br>
      <br></br>
    </>
  );
};
